<template>
  <div id="app" ref="app">
    <div class="one" v-bind:style="computedStyle">
        <video muted loop autoplay v-show="state === 0" v-on:canplaythrough.self.once="loaded++">
          <source id="mp4" src="../src/assets/videos/menu.mp4" type="video/mp4">
          <p>Your user agent does not support the HTML5 Video element.</p>
        </video>
        <img v-for="n in 74" :key="n" :src="frames[n - 1]" v-show="state === 1 && (counter === n || (counter === 0 && n === 1))" v-on:load.self.once="loaded++">
        <video muted loop autoplay v-show="state === 2" v-on:canplaythrough.self.once="loaded++">
          <source id="mp4" src="../src/assets/videos/gameover.mp4" type="video/mp4">
          <p>Your user agent does not support the HTML5 Video element.</p>
        </video>
    </div>
    <div class="two"></div>
    <div v-bind:style="computedStyle">
      <div id="konvacanvas" class="stack" v-bind:style="computedStyle"></div>
      <div class="stack" v-bind:style="computedStyle" v-show="state === 0">
        <b-button @click="start">Click Me</b-button>
      </div>
      <div class="stack rings" v-bind:style="opacity" v-show="state === 1 || state === 2">
        <video muted loop autoplay v-on:canplaythrough.self.once="loaded++">
          <source id="mp4" src="../src/assets/videos/rings.mp4" type="video/mp4">
          <p>Your user agent does not support the HTML5 Video element.</p>
        </video>
      </div>
    </div>
  </div>
</template>

<script>
const Konva = require('konva')

export default {
  name: 'App',
  data: function () {
    return {
      bodyWidth: 0,
      bodyHeight: 0,
      konvaCache: {},
      state: 0,
      counter: 0,
      time: 0,
      intervall: 1000,
      frames: [],
      sounds: {},
      loaded: 0
    }
  },
  computed: {
    dimensions: function () {
      const width = Math.floor(((this.bodyWidth - 20) / 2))
      const height = Math.floor(1072 * (width / 1872))
      return [width, height]
    },
    computedStyle: function () {
      return {
        width: `${this.dimensions[0]}px`,
        height: `${this.dimensions[1]}px`
      }
    },
    opacity: function () {
      let opacity = 0
      if (this.state === 2) {
        opacity = 1
      } else {
        if (this.counter > 8) opacity = (this.counter - 9) * 1 / 65
      }
      return {
        opacity,
        width: `${this.dimensions[0]}px`,
        height: `${this.dimensions[1]}px`
      }
    }
  },
  watch: {
    dimensions: function () {
      this.konvaCache.stage.size({
        width: this.dimensions[0],
        height: this.dimensions[1]
      })
    }
  },
  methods: {
    updateBodyDimensions: function () {
      this.bodyWidth = document.body.offsetWidth
      this.bodyHeight = document.body.bodyHeight
    },
    initKonva: function () {
      const stage = new Konva.Stage({
        container: 'konvacanvas',
        width: this.dimensions[0],
        height: this.dimensions[1]
      })
      const layer = new Konva.Layer()
      stage.add(layer)

      const imageObj = new Image()
      imageObj.src = require('../src/assets/kaa-small-1.png')
      const kaa = new Konva.Image({
        image: imageObj,
        width: 80,
        height: 65
      })
      kaa.on('mousedown', (event) => {
        event.target.destroy()
        layer.batchDraw()
        this.counter--
      })
      kaa.on('touchstart', (event) => {
        event.target.destroy()
        layer.batchDraw()
        this.counter--
      })
      this.konvaCache.kaa = kaa
      this.konvaCache.stage = stage
      this.konvaCache.layer = layer
    },
    initFrames: function () {
      for (let i = 1; i < 75; i++) {
        this.frames.push(require(`../src/assets/frames/frame-${i}.jpg`))
      }
    },
    initSounds: function () {
      this.sounds.gulp = new Audio(require('../src/assets/sounds/gulp.mp3'))
      this.sounds.ping = new Audio(require('../src/assets/sounds/ping.mp3'))
    },
    addKaa: function () {
      const clone = this.konvaCache.kaa.clone({
        x: Math.random() * (this.konvaCache.stage.width() - 80),
        y: Math.random() * (this.konvaCache.stage.height() - 65)
      })
      this.konvaCache.layer.add(clone)
      this.konvaCache.layer.batchDraw()
    },
    running: function () {
      if (this.state === 2) return
      if (this.time > 0 && this.time % 5 === 0) {
        this.intervall = 1000 - Math.floor(300 * Math.log(this.time / 5 + 1))
      }
      setTimeout(() => {
        if (this.state === 2) return
        this.addKaa()
        this.counter++
        if (this.counter < 75) {
          if (this.counter === 60) this.sounds.gulp.play()
          this.running()
        } else {
          this.sounds.ping.play()
          this.state = 2
        }
      }, this.intervall)
    },
    timer: function () {
      setTimeout(() => {
        if (this.state === 2) return
        if (this.counter < 75) {
          this.timer()
          this.time++
        }
      }, 1000)
    },
    start: function (n) {
      this.running()
      this.timer()
      this.state = 1
    }
  },
  mounted: function () {
    this.updateBodyDimensions()
    this.initKonva()
    this.initSounds()
    this.initFrames()
    window.addEventListener('resize', this.updateBodyDimensions)
  },
  beforeDestroy () {
    window.removeEventListener('resize', this.updateBodyDimensions)
  }
}
</script>

<style lang="scss">
html {
  height: 100%;
  background-color: rgb(218, 255, 218) !important;
}

body {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#app {
  width: 100%;
  background-color: white;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  border: 5px solid rgb(48, 131, 0);
}

.two {
  flex-grow: 1;
  background-color: rgb(48, 131, 0);
}

.stack {
  position: absolute;
}

.rings {
  pointer-events: none;
}
</style>
